import uniqid from 'uniqid'
import Typewriter from 'typewriter-effect'
import { about } from '../../portfolio'
import { useState } from 'react'
import { Timeline, TimelineItem } from 'vertical-timeline-component-for-react';
import './About.css'

const About = ({show}) => {
  const [ resume, setResume ] = useState(false)
  const { name, role, description, history } = about
  const fade = {
    opacity: show,
    transition: "all 1s ease-in"
  }
  
  return (
    <div className='about center' id='about'>
      {name && (
        <div className='about__intro'>
          <Typewriter
            onInit={(f) => {f
              .typeString(`생각을 구현하는 개발자<br /><p>${name}</p> 입니다.<br />:)`)
              .callFunction(() => {setResume(true)})
              .start()
            }}
            options={{
              strings: [
                '저는<br />좋은 개발자가 되기 위해<br />오늘도 노력합니다.',
                '첫째,<br />새로운 도전에<br />두려워하지 않습니다.',
                '둘째,<br />문제 해결을 위해<br />본질부터 다시 생각합니다.',
                '셋째,<br />나와 다른 생각을 가진<br />동료의 말에 귀 기울입니다.',
                '넷째,<br />목표를 성취하기 위해<br />스스로 점검하고 계획합니다.',
                '다섯째,<br />사회적 문제나 환경에<br />관심을 갖고 세상을 변화시킬<br />기술 개발을 상상합니다.',
              ],
              delay: 55,
              deleteSpeed: 20,
              pauseFor: 1000,
              autoStart: true
            }}
          />
        </div>
      )}
      {resume &&
      <a
        href='#career'
        className='about__resume'
      >
        Resume
      </a>}
      <h2
        className='section__title'
        id='career'
      >
        CAREER
      </h2>
      <div className='about__content' style={fade}>
        {role && <p className='about__role'>{role}.</p>}
        <p className='about__desc'>{description}</p>
        <Timeline lineColor={'#ddd'}>
          {history && history.map((data, index) => {
            return (
              <TimelineItem
                style={{
                  background: 'transparent',
                }}
                key={index}
                dateText={data.year && data.year}
              >
                <h4>{data.title && data.title}</h4>
                {data.events && data.events.map((event) => {
                  return (
                    <div key={uniqid()}>
                      <span>{event.date && event.date}</span>
                      <span>{event.subtitle && event.subtitle}</span>
                      <p>{event.description && event.description}</p>
                    </div>
                  )
                })}
              </TimelineItem>
            )})}
        </Timeline>
      </div>
    </div>
  )
}

export default About
