import { useContext, useState, useEffect } from 'react'
import { ThemeContext } from './contexts/theme'
import Header from './components/Header/Header'
import About from './components/About/About'
import Skills from './components/Skills/Skills'
import Projects from './components/Projects/Projects'
import ScrollToTop from './components/ScrollToTop/ScrollToTop'
import Footer from './components/Footer/Footer'
import './App.css'

const App = () => {
  const [{ themeName }] = useContext(ThemeContext)
  const [ showCareer, setCareer ] = useState(0)
  const [ showSkills, setSkills ] = useState(0)
  const [ showProjects, setProjects ] = useState(0)
  const handleScroll = () => {
    setCareer(
      window.innerHeight * 0.5 < document.getElementById('about').getBoundingClientRect().bottom &&
      document.getElementById('career').getBoundingClientRect().top < window.innerHeight * 0.7 ? 1 : 0
    )
    setSkills(
      window.innerHeight * 0.3 < document.getElementById('skills').getBoundingClientRect().bottom &&
      document.getElementById('skills').getBoundingClientRect().top < window.innerHeight * 0.7 ? 1 : 0
    )
    setProjects(
      window.innerHeight * 0.3 < document.getElementById('projects').getBoundingClientRect().bottom &&
      document.getElementById('projects').getBoundingClientRect().top < window.innerHeight * 0.7 ? 1 : 0
    )
  }

  useEffect(() => {
    const watch = () => {
      window.addEventListener('scroll', handleScroll);
    }
    watch()

    return () => {
      window.removeEventListener('scroll', handleScroll);
    }
  })

  return (
    <div id='top' className={`${themeName} app`}>
      <Header />

        <About show={showCareer}/>
        <Skills show={showSkills}/>
        <Projects show={showProjects}/>

      <ScrollToTop />
      <Footer />
    </div>
  )
}

export default App
