import uniqid from 'uniqid'
import { skills } from '../../portfolio'
import { useState, useEffect } from 'react'
import { Chart } from "react-google-charts";
import './Skills.css'

const Skills = ({show}) => {
  const { categories, data } = skills
  const fade = {
    opacity: show,
    transition: "all 1s ease-in"
  }

  return (
    <section className='section skills' id='skills'>
      <h2 className='section__title'>Skills</h2>
      <div style={fade}>
        {data &&
        <div>
          <p className='skills__chart'>
            Language Analysed by Linguist
          </p>
          <Chart
            data={data}
            options={{
              pieHole: 0.6,
              pieStartAngle: 260,
              fontName: 'Roboto',
              backgroundColor: 'transparent',
              pieSliceText: 'label',
              legend: 'none',
              slices: [
                {
                  color: '#50afad',
                  offset: 0.05
                },
                {
                  offset: 0.05
                },
                {
                  color: '#1a1aff',
                  offset: 0.2
                },
                {
                  color: '#e9a227',
                  offset: 0.05
                },
                {
                  color: '#a8a3a2',
                  offset: 0.2
                },
                {
                  color: '#4fec34',
                  offset: 0.1
                },
                {
                  color: '#f86372',
                  offset: 0.1
                },
                {
                  color: '#a833a2',
                  offset: 0.1
                },
              ],
              tooltip: {
                trigger: 'selection',
                showColorCode: true
              },
              chartArea: {
                top: '15%',
                width: '70%',
                height: '65%'
              },
            }}
            chartType='PieChart'
            width={'100%'}
            height={'500px'}
          />
        </div>}
        <ul className='skills__list'>
          {categories.map((category) => (
            category.stacks.map((stack) => (
              <li
                key={uniqid()}
                className='skills__list-item'
                style={{boxShadow: `${category.color} 0px 0px 3px 2px`}}
              >
                {stack}
              </li>
            ))
          ))}
        </ul>
      </div>
    </section>
  )
}

export default Skills