import uniqid from 'uniqid'
import './ProjectContainer.css'

const ProjectContainer = ({ project }) => (
  <div className='project'>
    <h4 className='project__title'>{project.name}</h4>
    <p className='project__date'>{project.date}</p>
    {project.description && (
      <ul className='project__description'>
        {project.description.map((desc) => (
          <li key={uniqid()}>{desc}</li>
        ))}
      </ul>
    )}
    {project.stacks && (
      <ul className='project__stack'>
        {project.stacks.map((item) => (
          <li key={uniqid()} className='project__stack-item'>
            {item}
          </li>
        ))}
      </ul>
    )}
    
    {project.link &&
    <a
      className='project__link btn btn--plain'
      href={project.link}
      target='_blank'
    >
      프로젝트 둘러보기
    </a>}

    {project.youtube.map((id) => (
    <div key={uniqid()} className='project__area'>
      <iframe
        className="project__youtube"
        title={project.name}
        src={`https://www.youtube.com/embed/${id}?rel=0&modestbranding=1`}
        frameBorder='0'
        allowFullScreen
      ></iframe>
    </div>
    ))}

  </div>
)

export default ProjectContainer
